@use 'base/mixins' as m;
@use 'base/variables' as v;

.navegacion{
    display: flex;
    flex-direction: column;
    @include m.tablet {
        flex-direction: row;
    }

    &__enlace{
        color: v.$blanco;
        font-size: 1.4rem;
        padding: 2rem;
        font-weight: v.$black;
        text-transform: uppercase;

        &:hover,
        &--actual{
            background-color: v.$blanco;
            color: v.$azul;
        }
    }
}